@font-face {
  font-family: 'Material Icons';
  font-style: normal;
  font-weight: 400;
  src: url(/scripts/packages/material-design-icons/iconfont/MaterialIcons-Regular.eot);
  /* For IE6-8 */
  src: local("Material Icons"), local("MaterialIcons-Regular"), url(/scripts/packages/material-design-icons/iconfont/MaterialIcons-Regular.woff2) format("woff2"), url(/scripts/packages/material-design-icons/iconfont/MaterialIcons-Regular.woff) format("woff"), url(/scripts/packages/material-design-icons/iconfont/MaterialIcons-Regular.ttf) format("truetype"); }

.material-icons, .hero-image-container.empty :after {
  font-family: 'Material Icons';
  font-weight: normal;
  font-style: normal;
  font-size: 24px;
  /* Preferred icon size */
  display: inline-block;
  line-height: 1;
  text-transform: none;
  letter-spacing: normal;
  word-wrap: normal;
  white-space: nowrap;
  direction: ltr;
  /* Support for all WebKit browsers. */
  -webkit-font-smoothing: antialiased;
  /* Support for Safari and Chrome. */
  text-rendering: optimizeLegibility;
  /* Support for Firefox. */
  -moz-osx-font-smoothing: grayscale;
  /* Support for IE. */
  font-feature-settings: 'liga'; }

.hero-image-container {
  background-color: #ccc; }
  .hero-image-container.empty {
    min-height: 200px .hero-image-container.empty; }
    .hero-image-container.empty :after {
      content: 'add_a_photo'; }
